import React from "react"
import { Link } from "gatsby"
import Carousel from "react-multi-carousel"
import "react-multi-carousel/lib/styles.css"
import { CustomLeftArrow, CustomRightArrow } from "./CustomArrows"
import classes from "./Styles/Makers.module.css"

import aiste from "../../../assets/img/balodana/home/makers/Aiste.jpg"
import samshek from "../../../assets/img/balodana/home/makers/Samshek.jpg"
import rochita from "../../../assets/img/balodana/home/makers/Rochita.jpg"
import ant from "../../../assets/img/balodana/home/makers/ANT.jpg"
import { PROJECT_LINK, PREVIEW } from "../../../project-config"

const Makers = () => {
  const responsive = {
    superLargeDesktop: {
      // the naming can be any, depends on you.
      breakpoint: { max: 4000, min: 3000 },
      items: 5,
    },
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 4,
    },
    tablet: {
      breakpoint: { max: 1023, min: 464 },
      items: 2,
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1,
    },
  }

  return (
    <div className={classes.makersContainer}>
      <span className={classes.meetOurMakers}>Meet Our Makers</span>
      <Carousel
        responsive={responsive}
        swipeable={true}
        draggable={true}
        infinite={true}
        // showDots={true}
        customLeftArrow={<CustomLeftArrow />}
        customRightArrow={<CustomRightArrow />}
      >
        <Link to={PREVIEW + "/designers/aiste-anaite-shop"}>
          <div className={classes.maker}>
            <img
              src={
                PROJECT_LINK +
                "/store/20180730431/assets/images/avatars/aiste-avatar.jpg"
              }
              alt="Aiste Anaite"
            />
            <span>Aiste Anaite</span>
          </div>
        </Link>
        <Link to={PREVIEW + "/designers/zaza-boutique-shop"}>
          <div className={classes.maker}>
            <img
              src={
                PROJECT_LINK +
                "/store/20180730431/assets/images/avatars/zaza-avatar.jpg"
              }
              alt="Alexandra Cornea"
            />
            <span>Alexandra Cornea</span>
          </div>
        </Link>
        <Link to={PREVIEW + "/designers/shantima-shop"}>
          <div className={classes.maker}>
            <img
              src={
                PROJECT_LINK +
                "/store/20180730431/assets/images/avatars/shantima-avatar.jpg"
              }
              alt="Alina Zilberman"
            />
            <span>Alina Zilberman</span>
          </div>
        </Link>
        <Link to={PREVIEW + "/designers/ashley-nell-tipton-shop"}>
          <div className={classes.maker}>
            <img
              src={
                PROJECT_LINK +
                "/store/20180730431/assets/images/avatars/ant-avatar.jpg"
              }
              alt="Ashley Nell Tipton"
            />
            <span>Ashley Nell Tipton</span>
          </div>
        </Link>
        <Link to={PREVIEW + "/designers/delia-von-hahn-shop"}>
          <div className={classes.maker}>
            <img
              src={
                PROJECT_LINK +
                "/store/20180730431/assets/images/avatars/delia-avatar.jpg"
              }
              alt="Delia von Hahn"
            />
            <span>Delia von Hahn</span>
          </div>
        </Link>
        <Link to={PREVIEW + "/designers/teyxo-shop"}>
          <div className={classes.maker}>
            <img
              src={
                PROJECT_LINK +
                "/store/20180730431/assets/images/avatars/teyxo-avatar.jpg"
              }
              alt="Lara Popa"
            />
            <span>Lara Popa</span>
          </div>
        </Link>
        <Link to={PREVIEW + "/designers/lino-piani-shop"}>
          <div className={classes.maker}>
            <img
              src={
                PROJECT_LINK +
                "/store/20180730431/assets/images/avatars/lino-avatar.jpg"
              }
              alt="Lino Piani"
            />
            <span>Lino Piani</span>
          </div>
        </Link>
      </Carousel>
    </div>
  )
}

export default Makers
