import React from "react";
import classes from "./Styles/PressLogos.module.css";
import CBS from "../../../assets/img/balodana/home/logos/CBS.png";
import NBC from "../../../assets/img/balodana/home/logos/NBC.png";
import BUST from "../../../assets/img/balodana/home/logos/BUST.png";
import ABC from "../../../assets/img/balodana/home/logos/abc.png";
import GLOSSY from "../../../assets/img/balodana/home/logos/glossy.png";
import FOX from "../../../assets/img/balodana/home/logos/fox-news.png";
import SELF from "../../../assets/img/balodana/home/logos/self.png";
import WGN from "../../../assets/img/balodana/home/logos/WGN.png";
import N1871 from "../../../assets/img/balodana/home/logos/1871.png";

const PressLogos = () => {
    return (
        <div className={classes.wrapper}>
            <div className={classes.press}>
                <div><img src={CBS} alt="CBS Logo"/></div>
                <div><img src={NBC} alt="NBC Logo"/></div>
                <div><img src={BUST} alt="BUST Logo"/></div>
                <div><img src={ABC} alt="ABC Logo"/></div>
                <div><img src={GLOSSY} alt="Glossy Logo"/></div>
                <div><img src={FOX} alt="Fox News Logo"/></div>
                <div><img src={SELF} alt="SELF Logo"/></div>
                <div><img src={WGN} alt="WGN9 Logo"/></div>
                <div><img src={N1871} alt="1871 Logo"/></div>
            </div>
        </div>
    )
}

export default PressLogos;